.hotel-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.hotel-wrapper {
  width: 100%;
  max-width: 1024px;
  position: relative;
}

.book-now {
  position: absolute;
  top: 10px;
  right: 0;
  border: none;
  padding: 10px 20px;
  background-color: #0071c2;
  color: white;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
}

.hotel-title {
  font-size: 20px;
  margin-bottom: 0;
}

.hotel-address {
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.hotel-distance {
  color: #0071c2;
  font-weight: 500;
}

.hotel-price-highlight {
  color: #008009;
  font-weight: 500;
}

.hotel-images {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.hotel-img-wrapper {
  width: 33%;
}

.hotel-img {
  width: 100%;
  object-fit: cover;
  cursor: pointer;
}

.hotel-details {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
}

.hotel-details-text {
  display: flex;
  flex-direction: column;
  gap: 10px;
  flex: 3;
}

.hotel-desc {
  font-size: 14px;
}

.hotel-details-price {
  flex: 1;
  background-color: #ebf3ff;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: fit-content;
}

.hotel-details-price > h1 {
  font-size: 18px;
  color: #555;
}

.hotel-details-price > span {
  font-size: 14px;
}

.hotel-details-price > h2 {
  font-weight: 300;
}

.hotel-details-price > button {
  border: none;
  padding: 10px 20px;
  background-color: #0071c2;
  color: white;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
}

.slider {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.613);
  z-index: 999;
  display: flex;
  align-items: center;
}

.slider-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider-img {
  width: 90%;
  height: 90%;
}

.close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 30px;
  color: lightgray;
  cursor: pointer;
}

.arrow {
  margin: 20px;
  font-size: 50px;
  color: lightgray;
  cursor: pointer;
}

@media screen and (max-width: 1024px) {
  .hotel-container {
    margin-top: 0;
  }
  .hotel-details-text {
    padding: 0 20px 0 20px;
  }
  .hotel-details {
    flex-direction: column;
  }
  .hotel-distance,
  .hotel-price-highlight {
    font-size: 14px;
  }
  .hotel-details-price {
    gap: 0;
  }
  .slider-img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
  .arrow {
    display: none;
  }
}
